import React, {useEffect, useState} from 'react'
import {InputNumber, Select, Space} from 'antd'
import RewardModel from 'stores/models/RewardModel'
import {MinusCircleOutlined} from '@ant-design/icons'

type OptionsType = {label: string; value: string}[]
export interface ProductOptionsType {
  label: string
  value: string
  options?: OptionsType
  optionType?: string
}

const RewardItem: React.FC<{
  reward: RewardModel
}> = ({reward}) => {
  const {
    placeholder,
    productOptions,
    noCount,
    setProduct,
    count,
    setCount,
  } = reward

  return (
    <div
      style={{
        marginBottom: 5,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'baseline',
      }}
    >
      <Space>
        <Select
          onChange={(v: string) => setProduct(v)}
          placeholder={placeholder}
          style={{width: 250}}
        >
          {productOptions.map((product: any, idx: number) => {
            const value = String(product.value)
            return (
              <Select.Option key={value || idx} value={value}>
                {product.label}
              </Select.Option>
            )
          })}
        </Select>
        {!noCount && (
          <Space>
            <InputNumber value={count} onChange={(v) => setCount(v)} />
            <span>수량</span>
          </Space>
        )}
      </Space>
      <MinusCircleOutlined onClick={reward.remove} />
    </div>
  )
}

export default RewardItem
